.sptgb__toc__wrapper {
	font-family: "lato", lato, helvetica, "helvetica neue" !important;
	background: #f7f7f7 !important;
	padding: 15px !important;
	border-radius: 5px !important;
	max-width: 600px !important;
}
.sptgb__toc__wrapper__header {
	display: flex !important;
	align-items: center !important;
}
.sptgb__toc__wrapper__header__headline {
	font-size: 20px !important;
	font-weight: bold !important;
}
.sptgb__toc__wrapper__header__close {
	margin-left: auto !important;
	color: #000 !important;
	text-decoration: none !important;
	padding: 7px 20px !important;
	border-radius: 3px !important;
	background: #fff !important;
	box-shadow: 0px 0px 9px rgba(0, 0, 0, 0.12) !important;
}
.sptgb__toc__wrapper__header__close:after {
	content: "Hide" !important;
}
.sptgb__toc__wrapper__header__close__active:after {
	content: "Show" !important;
}
.sptgb__toc__wrapper__content__hidden {
	display: none !important;
}
.sptgb__toc__wrapper__content ul {
	counter-reset: section !important;
	list-style-type: none !important;
	margin: 0 !important;
	padding: 0 !important;
}
.sptgb__toc__wrapper__content ul ul {
	padding-left: 20px !important;
	margin-bottom: 15px !important;
}
.sptgb__toc__wrapper__content ul ul ul {
	padding-left: 20px !important;
	margin-bottom: 0px !important;
}
.sptgb__toc__wrapper__content ul ul ul ul {
	padding-left: 20px !important;
	margin-bottom: 0px !important;
}
.sptgb__toc__wrapper__content ul ul ul ul ul {
	padding-left: 20px !important;
	margin-bottom: 0px !important;
}
.sptgb__toc__wrapper__content ul ul ul ul ul ul {
	padding-left: 20px !important;
	margin-bottom: 0px !important;
}
.sptgb__toc__wrapper__content__headline {
	font-size: 15px !important;
	margin: 8px 0 !important;
	line-height: 140% !important;
}
.sptgb__toc__wrapper__content__headline__sub {
	margin: 6px 0 !important;
}
.sptgb__toc__wrapper__content ul a {
	color: #3975ff !important;
	text-decoration: none !important;
}

.sptgb__toc__wrapper__content ul a:not([href]) {
	color: #3d5488 !important;
	text-decoration: none !important;
}

.sptgb__toc__wrapper__content ul li:before {
	counter-increment: section !important;
	content: counters(section, ".") ". " !important;
	color: #333 !important;
}

.sptgb__toc__wrapper br {
	display: none !important;
}
.sptgb__toc__wrapper button {
	background: #00bc87 !important;
	border: 0px solid #fff !important;
	color: #fff !important;
	padding: 10px 20px !important;
	cursor: pointer !important;
	font-size: 14px !important;
	border-radius: 3px !important;
	float: right !important;
}
